const black = document.querySelector('.black');
const natural = document.querySelector('.natural');
const white = document.querySelector('.white');
const blue = document.querySelector('.blue');

const front = document.querySelector('.front');
const tilted = document.querySelector('.tilted');
const side = document.querySelector('.side');
const frontBack = document.querySelector('.front-back');
const chosen = document.querySelector('.chosen');

black.addEventListener('click', () => {
  front.src = 'img/black/front.jpg';
  side.src = 'img/black/side.jpg';
  tilted.src = 'img/black/tilted.jpg';
  frontBack.src = 'img/black/front-back.jpg';
  chosen.src = 'img/black/front-back.jpg';
});
